@charset "UTF-8";
.FileTree {
  font-family: 'Roboto Slab', serif;
  position: absolute;
  left: 7px;
  top: 4px;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome and Opera */ }
  .FileTree ::-webkit-scrollbar {
    display: none; }
  .FileTree .tree {
    list-style: none;
    font-family: 'Hack';
    font-weight: lighter;
    margin: 0;
    padding: 0;
    margin-top: 7px; }
    .FileTree .tree .progeny {
      height: 95vh;
      overflow: auto; }
    .FileTree .tree ul {
      list-style: none;
      margin: 0;
      padding: 0;
      margin-left: 1.0em; }
    .FileTree .tree .view-link, .FileTree .tree .external-link, .FileTree .tree .folder {
      cursor: pointer;
      text-decoration: none;
      color: white;
      -webkit-transition: color .3s ease-out;
      -moz-transition: color .3s ease-out;
      -o-transition: color .3s ease-out;
      transition: color .3s ease-out; }
      .FileTree .tree .view-link:hover, .FileTree .tree .external-link:hover, .FileTree .tree .folder:hover {
        color: white; }
      .FileTree .tree .view-link.current, .FileTree .tree .external-link.current, .FileTree .tree .folder.current {
        color: #fff65f; }
    .FileTree .tree .view-link::before, .FileTree .tree .external-link::before {
      content: '∙';
      margin-right: 8px;
      font-weight: bold;
      font-style: normal; }
    .FileTree .tree .view-link:hover:not(.current)::after, .FileTree .tree .external-link:hover:not(.current)::after {
      content: '?'; }
    .FileTree .tree .folder.root {
      display: inline-block;
      overflow: hidden;
      padding-top: 11px;
      font-size: 19px;
      position: absolute;
      top: -7px;
      transition: color .3s ease-out, width .5s ease-in-out; }
      .FileTree .tree .folder.root + .toggle-container {
        top: -17px; }
    .FileTree .tree .folder {
      margin-left: 2px; }
    .FileTree .tree li {
      margin-left: 0.35em;
      padding-top: 20px;
      border-left: thin solid white; }
      .FileTree .tree li:not(.first-gen) {
        animation-name: fade-in;
        animation-duration: 1.2s;
        animation-timing-function: cubic-bezier(0.33, 0.56, 0.59, 1.01); }
      .FileTree .tree li.first-gen {
        animation-name: fade-in-delayed;
        animation-duration: 1.2s;
        animation-timing-function: cubic-bezier(0.49, 0.04, 0.58, 1); }
      .FileTree .tree li:last-child {
        border-left: none; }
      .FileTree .tree li::before {
        width: 0.9em;
        height: 0.6em;
        margin-right: 0.1em;
        vertical-align: top;
        border-bottom: thin solid white;
        content: "";
        display: inline-block; }
      .FileTree .tree li:last-child:before {
        border-left: thin solid white;
        height: 29px;
        margin-top: -20px; }
    .FileTree .tree .toggle-container {
      display: inline-block;
      width: 15px;
      height: 15px;
      position: relative;
      border-radius: 2px;
      left: -3px;
      top: 2px; }
      .FileTree .tree .toggle-container.hover {
        background: rgba(189, 189, 189, 0.2); }
      .FileTree .tree .toggle-container.active {
        box-shadow: inset 0px 0px 2px 1px rgba(116, 116, 116, 0.365); }
      .FileTree .tree .toggle-container.open {
        background: white; }
        .FileTree .tree .toggle-container.open .toggle::before, .FileTree .tree .toggle-container.open .toggle::after {
          background-color: black; }
      .FileTree .tree .toggle-container .toggle {
        position: relative;
        pointer-events: none; }
        .FileTree .tree .toggle-container .toggle::before, .FileTree .tree .toggle-container .toggle::after {
          content: '';
          display: block;
          position: absolute;
          top: 8px;
          right: -12px;
          width: 8px;
          height: 2px;
          margin-top: -1px;
          background-color: white;
          -webkit-transform-origin: 50% 50%;
          -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
          -webkit-transition: all 0.25s;
          transition: all 0.25s; }
        .FileTree .tree .toggle-container .toggle::before {
          -webkit-transform: rotate(-270deg);
          -ms-transform: rotate(-270deg);
          transform: rotate(-270deg);
          opacity: 0; }
        .FileTree .tree .toggle-container .toggle.collapsed::before {
          -webkit-transform: rotate(0deg);
          -ms-transform: rotate(0deg);
          transform: rotate(0deg);
          opacity: 1; }
        .FileTree .tree .toggle-container .toggle.collapsed::after {
          -webkit-transform: rotate(-90deg);
          -ms-transform: rotate(-90deg);
          transform: rotate(-90deg); }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in-delayed {
  0% {
    opacity: 0; }
  11% {
    opacity: 0; }
  100% {
    opacity: 1; } }
